.nav {
  display: inline-flex;
  flex-wrap: wrap;
}

.nav li {
  margin: 10px;
}

.nav li a {
  color: var(--color-dark);
}

.nav li a:hover {
  color: var(--color-primary);
}

.main-nav {
  display: flex;
  font-size: 0.8rem;
  right: -17rem;
  bottom: 21rem;
  position: fixed;
  z-index: 3000;
  transform: rotate(90deg);
}

.nav li::before {
  content: '';
}

/* --- MEDIA QUERY - SMALL --- */
@media screen and (max-width: 600px) {
  .main-nav {
    right: -18rem;
  }
}
