@font-face {
  font-family: Pano;
  src: url(./assets/pano-light.otf);
}

@font-face {
  font-family: Pano;
  src: url(./assets/pano-bold.otf);
  font-weight: bold;
}

@font-face {
  font-family: Pano;
  src: url(./assets/pano-regular.otf);
  font-weight: 300;
}

@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,200;0,300;0,400;0,500;0,600;1,200;1,300;1,400;1,500;1,600&display=swap');

* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

:root {
  /* container width */
  --container-width-lg: 60%;
  --container-width-md: 65%;
  --container-width-sm: 70%;

  --transition: all 400ms ease;

  --color-bg: #5f132f;
  --color-bg-dark: #780d41;
  --color-bg-variant: #5a1432;
  --color-highlight: #ed1b76;
  --color-primary: #249f9c;
  --color-primary-variant: #012d2d;
  --color-dark: #ffffff;
  --color-white: #121212;
  --color-light: #67d5d5;
  --color-btn-hover: #05aaa4;

  --color-1: #021d3b;
  --color-2: #371334;
  --color-3: #022526;
  --color-4: #3f061b;
  --color-5: #280939;
}

[data-theme='light'] {
  --color-bg: #f44786;
  --color-bg-dark: #e4036f;
  --color-bg-variant: #f5c5d7;
  --color-highlight: #ed1b76;
  --color-primary: #249f9c;
  --color-primary-variant: #037a76;
  --color-dark: #000000;
  --color-white: #ffffff;
  --color-light: #a5fffd;
  --color-btn-hover: #037a76;

  --color-1: #c3feff;
  --color-2: #99f0c0;
  --color-3: #c8f9af;
  --color-4: #f9e2ab;
  --color-5: #f7cfed;
}

[data-theme='dark'] {
  --color-bg: #5f132f;
  --color-bg-dark: #780d41;
  --color-bg-variant: #5a1432;
  --color-highlight: #ed1b76;
  --color-primary: #249f9c;
  --color-primary-variant: #012d2d;
  --color-dark: #ffffff;
  --color-white: #121212;
  --color-light: #a5fffd;
  --color-btn-hover: #05aaa4;

  --color-1: #021d3b;
  --color-2: #371334;
  --color-3: #022526;
  --color-4: #3f061b;
  --color-5: #280939;
}

body {
  font-family: 'Pano';
  color: var(--color-dark);
  line-height: 1.7;
}

html {
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  /* remove scrollbar */
  display: none;
}

/* --- GENERAL STYLES --- */
.container {
  width: var(--container-width-lg);
  margin: 0 auto;
}

h3,
h4,
h5 {
  font-weight: bold;
  color: var(--color-dark);
}

h1 {
  font-size: 2.5rem;
}

h2 {
  font-size: 1.8rem;
}

section {
  margin-top: 8rem;
}

section > h2 {
  text-align: center;
  color: var(--color-highlight);
  margin-bottom: 3rem;
}

.text-light {
  color: var(--color-light);
}

a {
  color: var(--color-primary);
  transition: var(--transition);
}

a:hover {
  color: var(--color-primary-variant);
}

.btn {
  text-align: center;
  width: max-content;
  display: inline-block;
  color: var(--color-lighter);
  padding: 0.5rem 1rem;
  border-radius: 0.4rem;
  cursor: pointer;
  border: 1.3px solid var(--color-dark);
  transition: var(--transition);
}

.btn:hover {
  background: var(--color-btn-hover);
  color: var(--color-white);
  border-color: transparent;
}

.btn-primary {
  background: var(--color-lighter);
  color: var(--color-white);
}

a .btn:hover {
  color: var(--color-btn-hover);
}

img {
  display: block;
  width: 100%;
  object-fit: cover;
}

/* --- MEDIA QUERY - MEDIUM --- */
@media screen and (max-width: 1024px) {
  .container {
    width: var(--container-width-md);
  }
  h2 {
    font-size: 1.65rem;
  }
}

section {
  margin-top: 6rem;
}

/* --- MEDIA QUERY - SMALL --- */
@media screen and (max-width: 600px) {
  .container {
    width: var(--container-width-sm);
  }
  h2 {
    font-size: 1.5rem;
  }
}

ul li:before {
  content: '. ';
  font-weight: 900;
}
